@media all {
  .printable {
    width: 80%;
    margin-left: auto;
    margin-right: auto;
  }

  .initiative-letterhead {
    font-size: 14pt;
    font-weight: bold;
  }
  .initiative-letterhead img {
    margin-right: 5pt;
  }

  .initiative-print-title {
    border: 1pt solid black;
    margin: 15pt 0;
    font-size: 12pt;
    font-weight: bold;
    text-align: center;
  }

  .print-table {
    width: 100%;
    display: block;
    border: 1pt solid black;
  }
  .print-table .print-table-header {
    background-color: lightgray;
    display: inline-block;
    width: 100%;
    padding-left: 5pt;
    font-size: 12pt;
    font-weight: bold;
    border-bottom: 1pt solid black;
  }
  .print-table .print-table-row {
    width: 100%;
    display: inline-block;
    min-height: 33pt;
    border-bottom: 1pt solid black;
  }
  .print-table .print-table-row::after {
    content: "";
    display: inline-block;
    clear: both;
  }
  .print-table .print-table-row:last-child {
    border-bottom: 0;
  }
  .print-table .print-table-cell {
    padding-left: 5pt;
    display: inline-block;
    float: left;
    border-right: 1pt solid black;
    min-height: 36pt;
  }
  .print-table .print-table-cell .cell-content {
    font-size: 10pt;
    word-wrap: break-word;
  }
  .print-table .checkbox-cell {
    width: 36pt;
  }
  .print-table .print-table-cell:last-child {
    border-right: 0;
  }
  .print-table .w33 {
    width: 33%;
  }
  .print-table .w66 {
    width: 66%;
  }
  .print-table .w50 {
    width: 50%;
  }

  .no-border {
    border: none !important;
  }

  .print-section-title {
    font-size: 12pt;
    font-weight: bold;
    margin-top: 15pt;
  }

  .print-initiative-legal-text {
    font-size: 10pt;
    margin-top: 80pt;
  }
}
@page {
  size: A4;
  margin: 17mm 17mm 11mm 11mm;
}
@media print {
  .process-header {
    display: none;
  }

  .main-nav {
    display: none;
  }

  .title-bar {
    display: none;
  }

  .layout-nav {
    display: none;
  }

  .button.expanded {
    display: none;
  }

  .page-break {
    page-break-before: auto;
  }

  .main-footer {
    display: none;
  }

  .mini-footer {
    display: none;
  }
}